import React, { Component } from 'react';
import { NavLink } from "react-router-dom";
import "./Sidenav.css";
import profileIcon from '../../media/profile.png';

export default class Sidenav extends Component {
    render() {
        return (
            <div className="sidenav">
                <div className="profile-img">
                    <a href="/"><img src={profileIcon} width="100" height="100" alt="Prateek Jha" /></a>
                    {/* <h3>Prateek Jha</h3> */}
                </div>
                <div className="nav-links">
                    <ul>
                        <li>
                            <NavLink to="/"><h5>About</h5></NavLink>
                        </li>
                        <li>
                            <NavLink to="/projects"><h5>Projects</h5></NavLink>
                        </li>
                        <li>
                            <NavLink to="/work"><h5>Work Experience</h5></NavLink>
                        </li>
                    </ul>
                </div>
                <div className="contact-links">
                    <ul>
                        <li><a href="https://twitter.com/_prateekjha" target="_blank"><i className="fa fa-twitter" aria-hidden="true"></i></a></li>
                        <li><a href="mailto:prateekj777@gmail.com"><i className="fa fa-envelope" aria-hidden="true"></i></a></li>
                        <li><a href="https://www.instagram.com/_prateekjha_" target="_blank"><i className="fa fa-instagram" aria-hidden="true"></i></a></li>
                    </ul>
                </div>
            </div>
        )
    }
}
