import React, { Component } from 'react';
import AnimationWrapper from '../AnimationWrapper';
import './Projects.css';

export default class Projects extends Component {
    render() {
        return (
            <div className="projects-grid">
                <AnimationWrapper>
                    <blockquote className="embedly-card" data-card-controls="0" data-card-theme="dark"><h4><a href="https://github.com/Quarantine-Projects/easy_definition">Quarantine-Projects/easy_definition</a></h4><p>It's a simple chrome extenstion to quickly get the meaning of higlighted text on currently opened tab. Download from Webstore Easily get the definition of any word by pressing Ctrl + Space. How to use: On any web page highlight the word using mouse pointer and Press CTRL+Space No need to open new tabs and loose sight of your working page even for a second.</p></blockquote>
                    <blockquote className="embedly-card" data-card-controls="0" data-card-theme="dark"><h4><a href="https://github.com/jha-prateek/explorer">jha-prateek/explorer</a></h4><p>An Android Application which will let visually impaired users to know the place and localize it within the given frame of picture, we are using a pre trained network for our object detection algorithm MSCOCO. Download APK from drive link in readme. - jha-prateek/explorer</p></blockquote>
                    <blockquote className="embedly-card" data-card-controls="0" data-card-theme="dark"><h4><a href="https://github.com/jha-prateek/ngo_matching">jha-prateek/ngo_matching</a></h4><p>A website to locate nearby NGO using your GPS coords.</p></blockquote>
                    <blockquote className="embedly-card" data-card-controls="0" data-card-theme="dark"><h4><a href="https://github.com/jha-prateek/Home">jha-prateek/Home</a></h4><p>A Home Automation Project. This is a mini-project for my 5th-Semester. It consists of an Android App, a Python Script for doing Machine Learning and Analytics on sensor data along with an Arduino Code for interacting with the Environment - jha-prateek/Home</p></blockquote>
                    <blockquote className="embedly-card" data-card-controls="0" data-card-theme="dark"><h4><a href="https://github.com/jha-prateek/NetWalker">jha-prateek/NetWalker</a></h4><p>A Domain based Web Search Engine made using the fusion of various Technologies. It works by taking the query from user and feeds it into the NLP module to extract chunks of words that is searched in pre-crawled Database formed using Beautiful Soup and MongoDB.</p></blockquote>

                </AnimationWrapper>
            </div>
        )
    }
}
